import { useState, useEffect, useRef } from 'react';
import io from 'socket.io-client'
import Message from './Message';
import './style.css';

const socket = io("http://localhost:3001");
const Chat = (props) => {
    const divRef = useRef(null);
    const [username, setUsername] = useState('');
    const [room, setRoom] = useState('');
    const [data, setData] = useState('');
    const [input, setInput] = useState('');
    const [messages, setMessages] = useState([]);
    const avtar = Math.floor(Math.random() * 8) + 1;

    useEffect(() => {
        divRef.current.scrollIntoView({ behavior: 'smooth' });
    });

    useEffect(() => {
        const { room, username } = props.match.params;

        setUsername(username)
        setRoom(room)

        socket.emit('userData', { username, room, avtar })

        socket.on('username', (username) => setData(username))
        socket.on('username', (username) => { setData(username)})

        socket.on('message', message => {
            setMessages(messages => [...messages, message]);
        });


    }, []);

    const sendData = (e) => {
        e.preventDefault();

        if (input) {
            socket.emit('sendMessage', input, () => setInput(''));
        }
    }

    return (
        <div className="container">
            <div className="col-md-12 col-lg-6 col-lg-offset-3">
                <div className="panel mt-5">
                    <div className="panel-heading">
                        <h3 className="panel-title">{room}</h3>
                    </div>

                    <div id="demo-chat-body" className="collapse in">
                        <div className="nano has-scrollbar" style={{ height: '380px' }}>
                            <div className="nano-content pad-all" tabIndex="0" style={{ right: '-17px' }}>
                                <ul className="list-unstyled media-block">
                                    {messages.map((message, i) => <div key={i}><Message message={message} name={username} /></div>)}
                                    <div ref={divRef} />
                                </ul>
                            </div>
                            <div className="nano-pane"><div className="nano-slider" style={{ height: '141px', transform: 'translate(0px, 0px)' }}></div></div></div>

                        <div className="panel-footer">
                            <div className="row">
                                <div className="col-xs-9">
                                    <input type="text" value={input} placeholder="Enter your text" className="form-control chat-input" 
                                    onChange={(e) => setInput(e.target.value)}
                                    onKeyPress={event => event.key === 'Enter' ? sendData(event) : null} />
                                </div>
                                <div className="col-xs-3">
                                    <button className="btn btn-primary btn-block" type="submit" onClick={sendData}>Send</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    )
}

export default Chat