import {useState} from 'react'
import './Join.css';

const Join = () => {
    const [name, setName] = useState('');
    const [room, setRoom] = useState('');

    return(
        <div className="joinOuterContainer">
            <div className="joinInnerContainer">
                <h1 className="heading">Chat Room</h1>
                <form action = {`/${room}/${name}`}>
                <div><input placeholder="Name" className="joinInput" type="text" onChange={(event)=>setName(event.target.value)} required/></div>
                <div><input placeholder="Room" className="joinInput mt-20" type="text" onChange={(event)=>setRoom(event.target.value)} required/></div>
                    <button className="button mt-20" type="submit">Sign In</button>
                </form>
            </div>
        </div>
    )
}

export default Join